<template>
  <div class="puffery">
      <swiper></swiper>
      <myself></myself>
  </div>
</template>

<script>
import SwiperBox from "../components/SwiperBox";
import Myslef from "../components/Myslef";
export default {
  name: "Puffery",
  data() {
    return {};
  },
  components: {
    swiper: SwiperBox,
    myself:Myslef
  }
};
</script>

<style>
.puffery{
    width: 740px;
    height: 100%;
}
</style>



