import Vue from 'vue'
import Router from 'vue-router'
import Homepage from '@/components/Homepage'
import Home from '@/components/Home'
import SwiperBox from '@/components/SwiperBox'
import Puffery from '@/components/Puffery'
import InformalEssay from '@/components/InformalEssay'
import ProjectNotes from '@/components/ProjectNotes'
import Share from '@/components/Share'
import Trajectory from '@/components/Trajectory'
import StudyNotes from '@/components/StudyNotes'
import Myslef from "@/components/Myslef";

Vue.use(Router)

export default new Router({
  routes: [{
      path: '/',
      name: 'Homepage',
      component: Homepage,
      'history': History
    },
    {
      path: '/home',
      name: 'Home',
      component: Home,
      children: [{
          path: '/puffery',
          name: 'Puffery',
          component: Puffery,
          children: [{
              path: '/swiper',
              name: 'SwiperBox',
              component: SwiperBox
            },
            {
              path: '/myslef',
              name: 'Myslef',
              component: Myslef
            }
          ]
        },
        {
          path: '/studyNotes',
          name: 'StudyNotes',
          component: StudyNotes
        },
        {
          path: '/informalEssay',
          name: 'InformalEssay',
          component: InformalEssay
        },
        {
          path: '/projectNotes',
          name: 'ProjectNotes',
          component: ProjectNotes
        },
        {
          path: '/share',
          name: 'Share',
          component: Share
        },
        {
          path: '/trajectory',
          name: 'Trajectory',
          component: Trajectory
        }
      ]
    },

  ]
})



