<template>
  <div class="home">
    <div class="interface">
      <div class="interfaceLeft">
        <div class="navM">
          <div class="xiaok">
            <img
              src="https://wx.qlogo.cn/mmopen/vi_32/ZyAM09LVwfOjVicXE7w3ussvX9QHO3rXKTyg2p0bRquUeCSJqqBzCesGxYBsAsZlQeO1GG4F5pSgIJgU3MIwzuQ/132"
              alt
            >
          </div>
          <div class="skill">
            <ul>
              <li>
                <img src="../assets/js.svg" title="JavaScript" alt>
              </li>
              <li>
                <img src="../assets/HTML5.svg" title="HTML5" alt>
              </li>
              <li>
                <img src="../assets/CSS3.svg" title="CSS3" alt>
              </li>
              <li>
                <img src="../assets/jQuery.svg" title="jQuery" alt>
              </li>
              <li>
                <img src="../assets/vuejs.svg" title="VueJs" alt>
              </li>
              <li>
                <img src="../assets/webpack.svg" title="Webpack" alt>
              </li>
            </ul>
          </div>
        </div>
        <div class="MyName">Xiao.K</div>
        <div class="navX">
          <div class="navwall">
            <ul>
              <!-- 笔记 -->
              <!-- router-link添加click事件会被阻止，添加一个修饰符native可以解决-->
              <router-link :class="[{default:istrue==index},{No:istrue!=index}]" :to="item.path" v-for="(item,index) in navs" :key="item.id" @click.native="istrue=index" tag="li">
                <img :src="item.imgSrc" :title="item.text" alt>
                {{item.text}}
              </router-link>
            </ul>
          </div>
        </div>
      </div>
      <div class="interfaceRight">
        <div class="navBar">
          <ul>
            <li>
              <img src="../assets/left.svg" title="前进" alt>
            </li>
            <li>
              <img src="../assets/right.svg" title="返回" alt>
            </li>
            <li>
              <img src="../assets/shuaxin.svg" title="刷新" alt>
            </li>
          </ul>
        </div>
        <div class="content">
          <router-view/>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Home",
  data() {
    return {
      navs: [
        {
          text: "自我吹捧",
          path: "/puffery",
          imgSrc: require("../assets/chuipeng.svg")
        },
        {
          text: "学习笔记",
          path: "/studyNotes",
          imgSrc: require("../assets/biji.svg")
        },
        {
          text: "项目详记",
          add: false,
          path: "/projectNotes",
          imgSrc: require("../assets/xiangmu.svg")
        },
        {
          text: "随笔梦花",
          path: "/informalEssay",
          imgSrc: require("../assets/xiaoshuo.svg")
        },
        {
          text: "实用分享",
          path: "/share",
          imgSrc: require("../assets/fenxiang.svg")
        },
        {
          text: "成长轨迹",
          path: "/trajectory",
          imgSrc: require("../assets/guiji.svg")
        }
      ],
      istrue:0
    };
  },
  methods: {
   
  },
  mounted() {
    this.$router.push({ name: "Puffery" });
  }
};
</script>

<style>
@import "../css/home.css";
</style>



