export const loading = function () {
  var load = setInterval(function () {
    const dots = document.getElementById("dots");
    dots.classList.remove('animate');
    setTimeout(function () {
      dots.classList.add('animate');
    }, 200);
  }, 3750);
  setTimeout(() => {
    clearInterval(load);
  }, 8000);
}



