<template>
  <div class="myself">
    <div class="myself_left">
      <div class="myself_life">
        <p v-for="item in mylife" :key="item.id">{{item.text}}</p>
      </div>
    </div>
    <div class="myself_right">
      <canvas id="myCanvas" width="300" height="300"></canvas>
    </div>
  </div>
</template>

<script>
// import Myself from "../js/Myself.js";
export default {
  name: "Myslef",
  data() {
    return {
      mylife: [
        { text: "首先，我是一个文艺的理工男" },
        { text: "户口本上是98年的" },
        { text: "不过我还是觉得自己是一个97年的" },
        { text: "毕竟那是我初次认识这个世界的时间" },
        { text: "这辈子最喜欢的事情就是创作" },
        { text: "小的时候喜欢画画" },
        { text: "将自己见到的东西留在纸上" },
        { text: "再大一点的时候" },
        { text: "我喜欢写小说" },
        { text: "将自己心中的世界留在我纸笔之间" },
        { text: "现在的我" },
        { text: "敲一敲代码,写一些文章,没事在瞎画一通" },
        { text: "表现我想要的样子" },
        { text: "这就是我现在的生活" }
      ]
    };
  },
  mounted() {
    // 取出画布  画笔
        var myCanvas = document.getElementById('myCanvas');
        var ctx = myCanvas.getContext('2d');

        // 定义基本得颜色值  画笔大小  阴影颜色 阴影半径
        ctx.strokeStyle = '#00ffff';
        ctx.lineWidth = 5;
        ctx.shadowBlur = 9;
        ctx.shadowColor = '#00ffff';

        // 根据时间绘制canvas时钟
        function renderTime() {
            var date = new Date();
            // 获得今天年月日
            var today = date.toDateString();
            // 获得当前时间  时分秒毫秒
            var h = date.getHours();
            var m = date.getMinutes();
            var s = date.getSeconds();
            var ms = date.getMilliseconds();

            var sliderS = s + (ms / 1000);
            var sliderM = m + (sliderS / 60);

            // 背景区域
            //创建渐变区域  渐变开始得坐标  开始圆的半径   结束圆的坐标  结束圆的半径
            var gradient = ctx.createRadialGradient(150, 150, 5, 150, 150, 300);
            // 填充颜色     createRadialGradient
            // addColorStop() 方法规定渐变对象中的颜色和位置 0 ~ 1
            gradient.addColorStop(0, '#03303a');
            gradient.addColorStop(1, '#000');
            // 填充背景
            ctx.fillStyle = gradient;
            ctx.fillRect(0, 0, 500, 500);

            // 绘制时钟
            // 小时
            ctx.beginPath();
            // 每一个小时对应 30deg  360/12 = 30 起始角度为-90deg所以再减去90Deg
            ctx.arc(150, 150, 100, degToRad(270), degToRad((h * 30) - 90));
            ctx.stroke();

            // 分钟
            ctx.beginPath();
            // 每一个小时对应 6deg  360/60 = 6 起始角度为-90deg所以再减去90Deg
            ctx.arc(150, 150, 85, degToRad(270), degToRad((sliderM * 6) - 90));
            ctx.stroke();

            // 秒
            ctx.beginPath();
            // 每一个小时对应 6deg  360/60 = 6 起始角度为-90deg所以再减去90Deg
            ctx.arc(150, 150, 70, degToRad(270), degToRad((sliderS * 6) - 90));
            ctx.stroke();

            // Date
            ctx.font = "16px Helvetica";
            ctx.fillStyle = 'rgba(0,255,255,1)';
            ctx.fillText(today, 90, 150);
            // Time
            ctx.font = "16px Helvetica";
            ctx.fillStyle = 'rgba(0,255,255,1)';

            // 如果小于零的数前面补零
            h = ('0' + h).slice(-2);
            m = ('0' + m).slice(-2);
            s = ('0' + s).slice(-2);
            // 将时间与字符串':'拼接 
            var time = h + ':' + m + ':' + s + ':' + ms;
            ctx.fillText(time, 106, 170);
        }
        setInterval(renderTime, 40);

        // 角度转化为弧度    弧度=角度*PI / 180
        function degToRad(deg) {
            return deg * Math.PI / 180;
        }
  },
  methods: {}
};
</script>

<style>
.myself {
  width: 710px;
  height: 330px;
  background: linear-gradient(
    45deg,
    #6fc7b5 0%,
    #13bdce,
    20%,
    #0094d9 40%,
    #5a3694 60%,
    #ee4d74 80%,
    #f58c58 100%
  );
  background-size: 400%;
  animation: gradient 7.5s ease-in-out infinite;
  position: fixed;
  left: 50%;
  top: 68%;
  transform: translate3d(-50%, -50%, 0) rotate(0deg);
  border-radius: 12px;
}
.myself .myself_left {
  width: 50%;
  height: 100%;
  background-color: rgba(241, 241, 241, 0.6);
  border-radius: 12px 0 0 12px;
  float: left;
}
.myself .myself_left .myself_life {
  height: 310px;
  margin: 18px 10px 10px 22px;
}
.myself .myself_left .myself_life p {
  color: #fff;
}
.myself .myself_right {
  width: 50%;
  height: 100%;
  background-color: rgba(241, 241, 241, 0.6);
  border-radius: 0 12px 12px 0;
  float: left;
}
.myself .myself_right #myCanvas {
  background-color: rgba(90, 86, 86, 0.6);
  position: absolute;
  top: 50%;
  left: 76%;
  transform: translate(-50%, -50%);
  border-radius: 12px;
  
}
</style>



