import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)
export default new Vuex.Store({
  state: {
    // 设置属性
    loading:true
  },
  getters: {
    // 获取属性的状态
    loading: state => state.loading
  },
  mutations: {
    // 改变属性的状态
    changeLoading(state,data) {
        state.loading = data;
    }
  },
  actions: {
    // 应用mutations
    
  }
})


