<!-- The ref attr used to find the swiper instance -->
<template>
  <div class="swiper-container">
    <div class="swiper-wrapper">
      <div class="swiper-slide">
        <img
          style="width:740px;height:200px"
          src="http://bmob-cdn-21722.b0.upaiyun.com/2018/10/08/a2d87af240a5fccc80ec11f3fa46cc4e.png"
          alt
        >
      </div>
      <div class="swiper-slide">
        <img
          style="width:740px;height:200px"
          src="http://bmob-cdn-21722.b0.upaiyun.com/2018/10/08/473888234037873e80997ff9b9e800c2.jpg"
          alt
        >
      </div>
      <div class="swiper-slide">
        <img
          style="width:740px;height:200px"
          src="http://bmob-cdn-21722.b0.upaiyun.com/2018/10/08/cb33b499401d7220806958b141e8b9f8.jpg"
          alt
        >
      </div>
    </div>
    <!-- 如果需要分页器 -->
    <div class="swiper-pagination"></div>
    <!-- 如果需要导航按钮 -->
    <div class="swiper-button-prev"></div>
    <div class="swiper-button-next"></div>
  </div>
</template>

<script>
// swiper options example:
import { swiper, swiperSlide } from "vue-awesome-swiper";
import Swiper from "swiper";
export default {
  name: "carrousel",
  components: {
    swiper,
    swiperSlide
  },
  data() {
    return {

    };
  },
  computed: {
    
  },
  mounted() {
    var that = this;
    that.$nextTick(function() {
      var mySwiper = new Swiper(".swiper-container", {
        direction: "horizontal",
        loop: true,
        pagination: {
          el: ".swiper-pagination",
          clickable: true
        },
        navigation: {
          nextEl: ".swiper-button-next",
          prevEl: ".swiper-button-prev"
        },
        autoplay: {
          delay: 3000,
          stopOnLastSlide: false,
          disableOnInteraction: false
        }
      });
    });
  }
};
</script>

 <style>
</style>
 


