<template>
  <div class="wrapper">
    <div class="logo">loading</div>
    <div class="dots animate" id="dots">
      <div class="dot"></div>
      <div class="dot"></div>
      <div class="dot"></div>
      <div class="dot"></div>
      <div class="dot"></div>
    </div>
  </div>
</template>

<script>
import { loading } from "@/js/loading.js";
export default {
  name: "Homepage",
  data() {
    return {};
  },
  beforeCreate() {
    var Loading = this.$store.state.loading;
    if (Loading) {
      loading();
      var that = this;
      setTimeout(() => {
        that.$store.commit("changeLoading", false);
        var Loading = this.$store.state.loading;
      }, 6000);
    }
  },
  mounted() {
    setTimeout(() => {
      var Loading = this.$store.state.loading;
      if (!Loading) {
        this.$router.push({ name: "Home" });
      }
    }, 8000);
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
@import "../css/loading.css";
</style>



